<template>
  <div class="app">
    <el-button @click="login" type="primary">登录</el-button>
    <el-button @click="register" type="primary">注册</el-button>
    <el-button @click="forget" type="primary">忘记密码</el-button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
    login(){
      let data={
        username:'18595838670',
        password:'lzy920512'
      }
      this.$axios.post('/loginIn',data).then(res=>{
        console.log(res)
      })
    },
    register(){
      this.$router.push('/register')
    },
    forget(){
      this.$router.push('/forgetPass')
    }
  }
}
</script>

